<template >
    <div class="body">
        <div class="header">
            <van-nav-bar title="购物车">
                <template #right>
                    <van-icon name="ellipsis" size="18" />
                </template>
            </van-nav-bar>
        </div>
        <div class="main">
            <!-- 购物车正体部分 -->
            <div v-if="items.length > 0">
                <div v-for="item of items" :key="item.scid">
                    <div class="shop-item">
                        <van-swipe-cell>
                            <van-card num="" :price="item.price" :desc="item.comment" :title="item.name" class="goods-card"
                                :thumb="base + item.img">
                                <template #tag>
                                    <van-radio-group v-model="tagRadio">
                                        <van-tag type="danger">标签</van-tag>
                                    </van-radio-group>
                                </template>
                                <!-- 清空右下角数量部分 -->
                                <template #num>

                                </template>
                            </van-card>
                            <template #right>
                                <van-button @click="delItem(item.scid)" square text="删除" type="danger"
                                    class="delete-button" />
                            </template>
                        </van-swipe-cell>
                    </div>
                    <br>
                </div>
            </div>
            <div v-else style="overflow: hidden;">
                <van-cell-group>
                    <van-cell value="您的购物车空空如也,快去添加吧" />
                </van-cell-group>
                <br>
                <img style="width: 95%;height: 100%; object-fit: cover;" src="../assets/images/shopcar/none.png" alt="">
            </div>
        </div>
        <van-divider>到底啦</van-divider>
        <div>
            <van-submit-bar :price="money" button-text="结账" @submit="onSubmit">
                <!-- <van-checkbox v-model="checked">全选</van-checkbox> -->
                <template #tip>
                    小贴士:请注意检查地址信息哦
                    <span @click="onClickEditAddress" style="text-decoration: underline;color: brown;">修改地址</span>
                </template>
            </van-submit-bar>
            <!-- 提交订单之后的弹出层 -->
            <van-popup v-model="show.buy" position="bottom" :overlay="true">
                <div style="text-align: left; color: gray; margin: 0.5rem;">
                    请选择您的收货地址
                </div>
                <div>
                    <!-- 此处的radio就是address.aid -->
                    <van-radio-group v-model="radio">
                        <van-cell-group>
                            <van-cell clickable @click="radio = address.aid" v-for="address in addressList"
                                :key="address.aid" :title="`${address.name}  ${address.tel}                      `"
                                :label="`${address.province} ${address.city} ${address.county}`">
                                <template #right-icon>
                                    <van-radio :name="address.aid" />
                                </template>
                                <!-- <van-radio :name="address.aid" /> -->
                            </van-cell>
                        </van-cell-group>
                    </van-radio-group>
                </div>
                <div>
                    <van-cell-group>
                        <van-field left-icon="chat-o" clearable label="备注" type="textarea" v-model="orderComment"
                            placeholder="请输入备注,最长100字" />
                    </van-cell-group>
                </div>
                <br>
                <div>
                    <van-button style="width: 100%;" type="danger" @click="submitOrder">提交订单</van-button>
                </div>
            </van-popup>

            <!-- 收款二维码弹框 -->
            <van-popup v-model="show.pay">
                <div>
                    <h3>
                        请支付
                    </h3>
                </div>
                <div>
                    <van-image
                        width="100%"
                        fit="cover"
                        src="../assets/images/pay.jpg"
                    />
                </div>
            </van-popup>
        </div>
        <!-- 页面底部 -->
        <div class="footer">
            <van-tabbar v-model="active.footer">
                <van-tabbar-item icon="home-o" to="/index" :router="true">
                    首页
                </van-tabbar-item>
                <van-tabbar-item icon="search" :router="true">
                    标签
                </van-tabbar-item>
                <van-tabbar-item icon="shopping-cart-o" to="/shoppingcar" :router="true">
                    购物车
                </van-tabbar-item>
                <van-tabbar-item icon="setting-o" to="/person" :router="true">
                    我的
                </van-tabbar-item>
            </van-tabbar>
        </div>
    </div>
</template>
<style scoped>
.header-nav {
    margin-top: 1rem;
}

.header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
}

.body {
    /* padding-top: 1rem; */
    background-color: #f1f5f7;
    min-height: 100vh;
}

.main {
    /* margin-top: 1rem; */
    padding-top: 4rem;
}

.van-submit-bar {
    bottom: 6%;
    position: fixed;
    z-index: 999;
}

.footer {
    height: 6%;
    width: 100%;
    position: absolute;
    bottom: 0;
    z-index: 999;
}

.van-cell__value--alone {
    color: gray;
    text-align: center;
}
</style>
<script>

import axios from 'axios'
import { Toast } from 'vant';

const options = {
    data() {
        return {
            active: {
                footer: ""
            },
            show: {
                buy: false,
                pay: false
            },
            tagRadio: false,
            value: "",
            checked: "",
            items: [],
            money: 0,
            radio: "1",
            orderComment: "",
            addressList: [],
        }
    },
    computed: {
        base() {
            return axios.defaults.baseURL;
        },
    },
    methods: {
        onSubmit() {
            /**
             * 用户点击结账之后触发
             * 弹出下一层
             */
            this.getAddress();
            this.show.buy = true;
        },
        onClickEditAddress() {

        },
        getAddress() {
            axios.get("/users/address").then((resp) => {
                if (resp.data.data == null) {
                    Toast("您尚未登录!");
                    return;
                }
                this.addressList = resp.data.data;
            });
        },
        submitOrder(){
            /**
             * 在弹出层提交订单后触发
             * 真正提交订单
             */
            let orders = [];
            for (let index = 0; index < this.items.length; index++) {
                const item = this.items[index];
                const order = {
                    iid: item.iid,
                    comment: this.orderComment,
                    aid: this.radio
                };
                orders.push(order);
            }
            axios.post("/users/buys",orders).then((resp)=>{
                console.log(resp.data)
                this.show.buy = false; // 关闭提交订单页面
                const _this = this;
                setTimeout(function() {
                    // 展示收款二维码
                    _this.show.pay = true;
                }, 2000);
            });
        },
        getItems() {
            axios.get("/users/caritems").then((resp) => {
                this.money = 0;
                if (resp.data.data == null) {
                    Toast(resp.data.msg)
                    return;
                }
                this.items = resp.data.data;
                // 计算价格
                const data = resp.data.data;
                for (let index = 0; index < data.length; index++) {
                    const element = data[index];
                    this.money += parseFloat(parseFloat(element.price).toFixed(2));
                }
                this.money *= 100;
            });
        },
        delItem(scid) {
            let form = new FormData();
            form.set("scid", scid);
            axios.post("/users/delsc", form).then((resp) => {
                Toast(resp.data.msg);
                if (resp.data.data) {
                    this.getItems();
                }
            });
        }
    },
    mounted() {
        // window.location.reload();
        console.log("shopping...")
        this.getItems();
    },
};
export default options;
</script>